h1, h2, h3, h4, h5, h6, p, img {
  border: 0;
  margin: 0;
  padding: 0;
}

html, body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
}

ul, ol {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: currentColor;
  text-decoration: none;
}

button {
  cursor: pointer;
  border: none;
}

.main-container {
  box-sizing: border-box;
  background-color: #ffb8ca;
  align-items: center;
  min-width: 320px;
  height: 502px;
  padding-left: 20px;
  display: flex;
  position: relative;
  overflow-x: hidden;
}

.left-container {
  z-index: 2;
  text-align: left;
  flex-direction: column;
  align-items: flex-start;
  display: flex;
  position: absolute;
  top: 8px;
}

.logo-svg {
  width: 199px;
  height: 50px;
}

.hero-title {
  letter-spacing: .04em;
  text-align: left;
  text-transform: uppercase;
  color: #fff;
  width: 218px;
  height: 106px;
  padding-top: 136px;
  font-family: Titan One, sans-serif;
  font-size: 26px;
  font-weight: 400;
  line-height: 29.77px;
}

.highlight {
  color: #d41443;
  text-shadow: 0 0 5px #0003;
  font-family: Titan One, sans-serif;
  font-size: 48px;
  font-weight: 400;
  line-height: 46.32px;
}

.buttons-container {
  gap: 8px;
  padding-top: 20px;
  display: flex;
}

.products-button, .how-made-button {
  text-align: center;
  cursor: pointer;
  color: #d41443;
  background-color: #fff;
  border: none;
  border-radius: 20px;
  padding: 10px 20px;
  font-family: DM Sans, sans-serif;
  font-size: 8px;
  font-weight: 400;
  line-height: 10.42px;
  transition: background-color .25s ease-in-out;
}

.products-button:hover, .how-made-button:hover {
  color: #fff;
  background-color: #d41443;
}

.paragraph-icon-container, .milk-bottle, .description-paragraph {
  display: none;
}

.center-container {
  justify-content: center;
  align-items: center;
  display: flex;
}

.desktop-navbar {
  display: none;
}

.ice-cream-image {
  cursor: pointer;
  max-width: 100%;
  transition: transform .4s;
  position: absolute;
  top: 100px;
  right: 32px;
}

.ice-cream-image > svg {
  min-width: 346px;
  height: 344px;
  position: absolute;
  inset: -9px auto auto -81px;
}

.ice-cream-image > img {
  width: 171px;
  height: auto;
  position: relative;
}

.ice-cream-image:hover {
  transform: scale(1.1);
}

.right-container {
  z-index: 2;
  flex-direction: column;
  align-items: flex-end;
  display: flex;
  position: absolute;
  top: 27px;
  right: 20px;
}

.header-right-container {
  align-items: center;
  display: flex;
}

.icon-burger-menu {
  border: 1px;
  width: 20px;
  height: 12px;
}

.buy-now-container, .stats-container, .person-container {
  display: none;
}

