.popup {
  z-index: 10;
  background-color: #e17992;
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
}

.is-hidden {
  visibility: hidden;
}

.close-button-container {
  justify-content: flex-end;
  padding-top: 20px;
  padding-right: 32px;
  display: flex;
}

.popup-close-btn {
  transition-duration: .25s;
  transition-timing-function: linear;
  transition-delay: 0;
  width: 13.48px;
  height: 14px;
}

.popup-close-btn:hover {
  cursor: pointer;
  transform: rotate(90deg);
}

.popup-content {
  flex-direction: column;
  align-items: flex-start;
  display: flex;
}

.btn-buy-popup {
  text-align: center;
  color: #d41443;
  background: #fff;
  border-radius: 22px;
  justify-content: center;
  align-items: center;
  gap: 14px;
  width: 186px;
  height: 44px;
  margin: 24px auto 0;
  font-family: DM Sans;
  font-size: 16px;
  font-weight: 700;
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

.btn-buy-popup:hover {
  cursor: pointer;
  opacity: .83;
}

.arrow-svg {
  width: 8px;
  height: 8px;
}

.popup-list {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.popup-item a {
  text-align: left;
  color: #fff;
  border-top: 1px solid #e18298;
  width: 100vw;
  padding: 12px 0 12px 20px;
  font-family: DM Sans;
  font-size: 14px;
  font-weight: 700;
  line-height: 18.23px;
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
  display: block;
}

.popup-item:last-child {
  border-bottom: 1px solid #e18298;
}

.popup-item a:hover {
  cursor: pointer;
  color: #d41443;
}

@media screen and (width >= 1158px) {
  .popup {
    display: none;
  }
}

.products-section {
  box-sizing: border-box;
  width: 100%;
  padding: 60px 0;
  display: block;
}

.products-container {
  flex-direction: column;
  align-items: center;
  max-width: 480px;
  margin: 0 auto;
  display: flex;
}

.section-title-container {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.subsection-title, .section-title {
  text-align: center;
  text-transform: uppercase;
  font-family: Titan One;
  font-weight: 400;
}

.subsection-title {
  color: #ffa5ba;
  margin-top: 0;
  margin-bottom: 15px;
  font-size: 18px;
  line-height: 20.61px;
}

.section-title {
  color: #d41443;
  letter-spacing: .04em;
  margin-top: 0;
  margin-bottom: 150px;
  font-size: 34px;
  line-height: 38.93px;
}

.products-cards {
  grid-template-columns: 1fr;
  gap: 150px;
  display: grid;
}

.product-item {
  box-sizing: border-box;
  border-radius: 24px;
  flex-direction: column;
  align-items: center;
  width: 280px;
  height: 489px;
  transition: background-color .35s cubic-bezier(.4, 0, .2, 1) 50ms, box-shadow .35s cubic-bezier(.4, 0, .2, 1) 50ms, font-size .35s cubic-bezier(.4, 0, .2, 1) 50ms, line-height .35s cubic-bezier(.4, 0, .2, 1) 50ms;
  display: flex;
  position: relative;
}

.product-item:first-child {
  background-color: #ffb8ca;
}

.product-item:nth-child(2) {
  background-color: #f0d1a5;
}

.product-item:nth-child(3) {
  background-color: #c2e297;
}

.product-item:before {
  content: "";
  z-index: 2;
  background-position: center;
  background-size: cover;
  width: 280px;
  height: 280px;
  position: absolute;
  top: -30%;
  left: 0;
}

.product-item:first-child:before {
  background-image: url("home-pic1-1x.082cbc4b.png");
}

.product-item:nth-child(2):before {
  background-image: url("home-pic2-1x.7e0b3409.png");
}

.product-item:nth-child(3):before {
  background-image: url("home-pic3-1x.5403a7d9.png");
}

@media (-webkit-device-pixel-ratio >= 2), (resolution >= 192dpi) {
  .product-item:first-child:before {
    background-image: url("home-pic1-2x.c40b398c.png");
  }

  .product-item:nth-child(2):before {
    background-image: url("home-pic2-2x.f16abddb.png");
  }

  .product-item:nth-child(3):before {
    background-image: url("home-pic3-2x.7f69d36d.png");
  }
}

.product-name:after {
  content: "...";
  text-align: center;
  color: #fff;
  padding: 32px 127px 0;
  font-size: 20px;
  display: block;
}

.info-product-card {
