.products-section {
  width: 100%;
  display: block;
  padding: 60px 0;
  box-sizing: border-box;
}

.products-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 480px;
  margin: 0 auto;
}

.section-title-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.subsection-title,
.section-title {
  font-family: 'Titan One';
  text-align: center;
  text-transform: uppercase;
  font-weight: 400;
}

.subsection-title {
  font-size: 18px;
  line-height: 20.61px;
  color: rgba(255, 165, 186, 1);
  margin-top: 0;
  margin-bottom: 15px;
}

.section-title {
  font-size: 34px;
  color: rgba(212, 20, 67, 1);
  margin-top: 0;
  margin-bottom: 150px;
  line-height: 38.93px;
  letter-spacing: 0.04em;
}

.products-cards {
  display: grid;
  grid-template-columns: 1fr;
  gap: 150px;
}

.product-item {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  box-sizing: border-box;
  border-radius: 24px;
  width: 280px;
  height: 489px;
  transition: 0.35s cubic-bezier(0.4, 0, 0.2, 1) 50ms;
  transition-property: background-color, box-shadow, font-size, line-height;
}

.product-item:first-child {
  background-color: rgba(255, 184, 202, 1);
}
.product-item:nth-child(2) {
  background-color: #f0d1a5;
}
.product-item:nth-child(3) {
  background-color: #c2e297;
}
.product-item::before {
  content: '';
  position: absolute;
  top: -30%;
  left: 0;
  width: 280px;
  height: 280px;
  background-size: cover;
  background-position: center;
  z-index: 2;
}

.product-item:nth-child(1)::before {
  background-image: url('../images/products/home-pic1-1x.png');
}

.product-item:nth-child(2)::before {
  background-image: url('../images/products/home-pic2-1x.png');
}

.product-item:nth-child(3)::before {
  background-image: url('../images/products/home-pic3-1x.png');
}

@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi) {
  .product-item:nth-child(1)::before {
    background-image: url('../images/products/home-pic1-2x.png');
  }

  .product-item:nth-child(2)::before {
    background-image: url('../images/products/home-pic2-2x.png');
  }

  .product-item:nth-child(3)::before {
    background-image: url('../images/products/home-pic3-2x.png');
  }
}
.product-name::after {
  content: '...';
  display: block;
  text-align: center;
  font-size: 20px;
  color: #fff;
  padding: 32px 127px 0;
}

.info-product-card {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.product-name {
  font-family: 'Titan One', sans-serif;
  color: rgba(255, 255, 255, 1);
  text-transform: uppercase;
  font-size: 20px;
  font-weight: 400;
  line-height: 22.9px;
  letter-spacing: 0.04em;
  text-align: center;
  padding-top: 160px;
}

.description-product {
  width: 216px;
  height: 124px;
  text-align: center;
  color: #fff;
  font-family: 'DM Sans';
  font-size: 14px;
  font-weight: 700;
  line-height: 21.28px;
  letter-spacing: 0.04em;
  padding: 32px 32px 0;
}

.arrow-right {
  margin: auto;
}

.icon-arrow-container {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  top: 409px;
  left: 50%;
  transform: translateX(-50%);
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-color: #fff;
  transition: 0.35s cubic-bezier(0.4, 0, 0.2, 1) 0ms;
  transition-property: width, height, transform, box-shadow, background-color,
    fill;
}

.icon-arrow-container svg {
  width: 18px;
  height: 18px;
  fill: #d41443;
  transition: 0.35s cubic-bezier(0.4, 0, 0.2, 1) 0ms;
  transition-property: transform, fill;
}

// Tablet
@media screen and (min-width: 768px) {
  .products-container {
    max-width: 768px;
  }
  .section-title-container {
    margin-bottom: 40px;
  }
  .subsection-title {
    font-size: 26px;
    line-height: 29.77px;
  }
  .section-title {
    font-size: 48px;
    line-height: 54.96px;
  }

  .products-cards {
    grid-template-columns: repeat(3, 1fr);
    gap: 16px;
  }

  .product-item {
    width: 224px;
    height: 473px;
  }

  .product-item:hover .arrow-right {
    transform: rotate(-90deg);
    cursor: pointer;
    fill: #fff;
  }
  .product-item:hover .icon-arrow-container {
    border-radius: 35%;
    background-color: #d41443;
    width: 50px;
    height: 50px;
    box-shadow: 0 0 7px rgba(0, 0, 0, 0.15);
  }
  .product-item:first-child:hover {
    box-shadow: 0 5px 15px rgba(168, 3, 85, 0.632);
    background-color: #d97596;
  }

  .product-item:nth-child(2):hover {
    box-shadow: 0 5px 15px rgba(229, 152, 11, 0.632);
    background-color: #f1ca81;
  }

  .product-item:nth-child(3):hover {
    box-shadow: 0 5px 15px rgba(1, 104, 27, 0.632);
    background-color: #94bd60;
  }
  .product-item:hover .description-product {
    font-size: 15px;
    letter-spacing: 0.48;
    line-height: 22px;
  }
  .product-item:hover h3 {
    font-size: 22px;
  }

  .product-item::before {
    width: 224px;
    height: 224px;
  }

  .product-name {
    padding-top: 118px;
  }

  .product-name::after {
    padding: 32px 99px 0;
  }

  .description-product {
    width: 188px;
    height: 150px;
    line-height: 24.64px;
    padding: 32px 18px 0;
  }
  .icon-arrow-container {
    top: 393px;
  }
}

// Desktop
@media screen and (min-width: 1158px) {
  .products-container {
    max-width: 1158px;
  }
  .section-title-container {
    margin-bottom: 120px;
  }
  .subsection-title {
    font-size: 30px;
    line-height: 34.35px;
  }
  .section-title {
    font-size: 58px;
    line-height: 66.41px;
    letter-spacing: 0.06em;
  }
  .subsection-title:hover {
    cursor: default;
    animation: letterspc;
    animation-duration: 2s;
    animation-timing-function: ease-in-out;
    animation-delay: 0s;
    animation-iteration-count: 1;
    animation-direction: normal;
    animation-fill-mode: forwards;
  }
  .section-title:hover {
    cursor: default;
    animation: letterspc2;
    animation-duration: 2s;
    animation-timing-function: ease-in-out;
    animation-delay: 0s;
    animation-iteration-count: 1;
    animation-direction: normal;
    animation-fill-mode: forwards;
  }
  @keyframes letterspc {
    0% {
      letter-spacing: 0.4em;
    }
    100% {
      letter-spacing: 0.04em;
    }
  }
  @keyframes letterspc2 {
    0% {
      letter-spacing: 0.6em;
    }
    100% {
      letter-spacing: 0.06em;
    }
  }

  .products-cards {
    grid-template-columns: repeat(3, 1fr);
    gap: 32px;
  }

  .product-item {
    width: 368px;
    height: 535px;
  }

  .product-item::before {
    width: 368px;
    height: 394px;
    top: -50%;
  }

  .product-name {
    padding-top: 165px;
    padding-left: 94px;
    padding-right: 93px;
  }

  .product-name::after {
    padding: 32px 171px 0;
  }

  .description-product {
    width: 304px;
    height: 134px;
    line-height: 28.16px;
    padding: 32px 32px 0;
    font-size: 16px;
  }

  .icon-arrow-container {
    top: 435px;
    width: 38.21px;
  }
}
